<template>
  <div class="add">
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form :model="form">
        <el-form-item label="上级分类" :label-width="formLabelWidth">
          <el-select v-model="form.pid">
            <el-option label="" value="" disabled>--请选择--</el-option>
            <!-- value值为变量 -->
            <el-option label="顶级分类" :value="0"></el-option>
            <el-option v-for="item in list" :key="item.id" :label="item.catename" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="分类名称" :label-width="formLabelWidth">
          <el-input v-model="form.catename"></el-input>
        </el-form-item>
        <!-- 自定义上传图片 -->
         <el-form-item label="图片" :label-width="formLabelWidth">
            <div class="box-img">
              <h3>+</h3>
              <img v-if="imageUrl" :src="imageUrl" >
              <input type="file" @change="changeImg1">
            </div>
        </el-form-item>

        <!-- element-ui上传图片 -->
        <!-- <el-form-item label="图片" :label-width="formLabelWidth">
            <el-upload
                  class="avatar-uploader"
                  action="#"
                  :show-file-list="false"
                  :on-change="changeImg"
                  >
                  <img v-if="imageUrl" :src="imageUrl" class="avatar">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
        </el-form-item> -->
        <el-form-item label="菜单状态" :label-width="formLabelWidth">
          <el-switch
            v-model="form.status"
            active-color="blue"
            inactive-color="grey"
            :active-value="1"
            :inactive-value="2"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="confirm" v-if="info.isAdd">确 定</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { warningAlert, successAlert } from '../../../utils/alert';
import { addCate, oneCate, updateCate } from '../../../utils/request';
import {mapGetters,mapActions} from 'vuex'
export default {
  props:['info'],
  data(){
    return {
      form:{
        pid:0,
        catename:'',
        img:'',
        status:1,
      },
      formLabelWidth:'120px',
      imageUrl: ''
    }
  },
  computed:{
    ...mapGetters({
      "list":"cate/cateList"
    })
  },
  methods:{
    ...mapActions({
      "requestCateList":"cate/cateListActions"
    }),
    changeImg1(e){
      var file = e.target.files[0];

      if(file.size > 2*1024*1024){
         warningAlert('图片大小超过2M,请重新上传')
          return
      }
       // 处理文件后缀
      var ext = ['.png','.jpg','.jpeg','.gif'];
      // 获取文件后缀
      var extName = file.name.slice(file.name.lastIndexOf('.'))
      // 查看后缀在不在指定的后缀名中
      if(!ext.some(item=>item==extName)){
        warningAlert('文件格式不正确!');
        return
      }

      this.imageUrl = URL.createObjectURL(file)
      this.form.img = file

    },
    // 改变图片时被触发
    changeImg(e){
      // 处理文件大小
      if(e.size > 2*1024*1024){
        warningAlert('图片大小超过2M,请重新上传')
        return
      }
      // 处理文件后缀
      var ext = ['.png','.jpg','.jpeg','.gif'];
      // 获取文件后缀
      var extName = e.name.slice(e.name.lastIndexOf('.'))
      // 查看后缀在不在指定的后缀名中
      if(!ext.some(item=>item==extName)){
        warningAlert('文件格式不正确!');
        return
      }

      // 生成要显示的图片
      var file = e.raw;
      // 做成要显示的图片
      this.imageUrl = URL.createObjectURL(file);
      // 存入form中
      this.form.img = file;
    },
    cancel(){
      this.info.show = false;
      this.form = {
         pid:0,
          catename:'',
          img:'',
          status:1,
      };
      this.imageUrl = '';
    },
    confirm(){
      // 发起请求
      addCate(this.form).then(res=>{
        successAlert(res.data.msg)
        this.cancel()
        this.requestCateList()
      })
    },
    getDetail(id){
      oneCate({id}).then(res=>{
        this.form = res.data.list;
        // 将图片展示出来
        this.imageUrl = this.imgUrl+this.form.img;
        this.form.id = id
      })
    },
    update(){
     updateCate(this.form).then(res=>{
        successAlert(res.data.msg);
        this.cancel()
        this.requestCateList()
     })
    }
  },
  mounted(){
    this.requestCateList()
  }
}
</script>

<style scoped>
  .box-img{
    width:150px;
    height:150px;
    border:1px dashed pink;
    position: relative;
  }
  .box-img h3{
    width:100%;
    height: 100%;
    line-height: 150px;
    text-align: center;
  }
  .box-img img{
    width:100%;
    height: 100%;
    position: absolute;
    left:0;
    top:0;
  }
  .box-img input{
    width: 100%;
    height: 100%;
    position: absolute;
    left:0;
    top:0;
    opacity: 0;
  }
  .avatar-uploader >>> .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader >>> .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



